<template>
    <div id="container"></div>
    <div class="info">
</div>
</template>


<script>
import AMapLoader from '@amap/amap-jsapi-loader'

export default{
    mounted() {
    AMapLoader.load({
    "key": "88c17c713cd24a59ad64b7e256449d1a",              // Áî≥ËØ∑Â•ΩÁöÑWebÁ´ØÂºÄÂèëËÄÖKeyÔºåÈ¶ñÊ¨°Ë∞ÉÁî® load Êó∂ÂøÖÂ°´
    "version": "2.0",   // ÊåáÂÆöË¶ÅÂä†ËΩΩÁöÑ JSAPI ÁöÑÁâàÊú¨ÔºåÁº∫ÁúÅÊó∂ÈªòËÆ§‰∏∫ 1.4.15
    "plugins": ['AMap.Scale','AMap.ToolBar','AMap.Geolocation','AMap.MapType'],           // ÈúÄË¶Å‰ΩøÁî®ÁöÑÁöÑÊèí‰ª∂ÂàóË°®ÔºåÂ¶ÇÊØî‰æãÂ∞∫'AMap.Scale'Á≠â
    }).then((AMap)=>{
    let map = new AMap.Map('container',{
        resizeEnable: true
    });
    let geolocation = new AMap.Geolocation({
    enableHighAccuracy: true,//ÊòØÂê¶‰ΩøÁî®È´òÁ≤æÂ∫¶ÂÆö‰ΩçÔºåÈªòËÆ§:true
        timeout: 10000,          //Ë∂ÖËøá10ÁßíÂêéÂÅúÊ≠¢ÂÆö‰ΩçÔºåÈªòËÆ§ÔºöÊó†Á©∑Â§ß
        maximumAge: 0,           //ÂÆö‰ΩçÁªìÊûúÁºìÂ≠ò0ÊØ´ÁßíÔºåÈªòËÆ§Ôºö0
        convert: true,           //Ëá™Âä®ÂÅèÁßªÂùêÊ†áÔºåÂÅèÁßªÂêéÁöÑÂùêÊ†á‰∏∫È´òÂæ∑ÂùêÊ†áÔºåÈªòËÆ§Ôºötrue
        showButton: false,        //ÊòæÁ§∫ÂÆö‰ΩçÊåâÈíÆÔºåÈªòËÆ§Ôºötrue
        showMarker: true,        //ÂÆö‰ΩçÊàêÂäüÂêéÂú®ÂÆö‰ΩçÂà∞ÁöÑ‰ΩçÁΩÆÊòæÁ§∫ÁÇπÊ†áËÆ∞ÔºåÈªòËÆ§Ôºötrue
        showCircle: true,        //ÂÆö‰ΩçÊàêÂäüÂêéÁî®ÂúÜÂúàË°®Á§∫ÂÆö‰ΩçÁ≤æÂ∫¶ËåÉÂõ¥ÔºåÈªòËÆ§Ôºötrue
        panToLocation: true,     //ÂÆö‰ΩçÊàêÂäüÂêéÂ∞ÜÂÆö‰ΩçÂà∞ÁöÑ‰ΩçÁΩÆ‰Ωú‰∏∫Âú∞Âõæ‰∏≠ÂøÉÁÇπÔºåÈªòËÆ§Ôºötrue
        zoomToAccuracy:true      //ÂÆö‰ΩçÊàêÂäüÂêéË∞ÉÊï¥Âú∞ÂõæËßÜÈáéËåÉÂõ¥‰ΩøÂÆö‰Ωç‰ΩçÁΩÆÂèäÁ≤æÂ∫¶ËåÉÂõ¥ËßÜÈáéÂÜÖÂèØËßÅÔºåÈªòËÆ§Ôºöfalse
    });
    map.addControl(geolocation);
    map.addControl(new AMap.Scale());
    map.addControl(new AMap.ToolBar());
    map.addControl(new AMap.MapType());
    map.add(new AMap.Marker({
            position:map.getCenter()
        }));
    geolocation.getCurrentPosition();
    }).catch(e => {
        console.log(e);
    });
},
}
</script>

<style scoped>
    body {
        margin: 0;
        padding: 0
    }

    html,
    body,
    #container {
        width: 100%;
        height: 100%;
    }
</style>