<script>
import { ref,reactive,onMounted } from 'vue'
import TianAn from '../components/TianAn.vue'
import * as chart from 'echarts'
import axios from '../utils/axios'

let myChart = null

export default {
  components:{
    TianAn
  },

  setup() {
    const loadChart = (e) => {
      if(myChart) {
        myChart.dispose()
      }
      console.log(e.props.name)
      axios.get(`/chart/score/${e.props.name}`)
      .then((res) => {
      const option = res.data
      myChart = chart.init(document.getElementById(`${e.props.name}`));
      myChart.setOption(option);
      }).catch((x) => {
        console.log(x)
      })
    }
    const initChart = () => {
      if(myChart) {
        myChart.dispose()
      }
      axios.get(`/chart/score/${state.activeName}`)
      .then((res) => {
      const option = res.data
      myChart = chart.init(document.getElementById(`${state.activeName}`));
      myChart.setOption(option);
      }).catch((x) => {
        console.log(x)
      })
    }
     const tableC = ref(null)
     const checkTable = () => {
       tableC.value.open()
     }
     const state = reactive({
      activeName:"all"
     })
     onMounted(() => {
       initChart()
     })
      return {
        ...state,
        checkTable,
        loadChart,
        tableC
      }
  }
}
</script>

